"use client";

import { useEffect, useRef, FC } from "react";
import { gsap } from "gsap";
import type { GridItem } from "@/data";

interface GridMotionProps {
  items?: GridItem[];
  gradientColor?: string;
}

const GridMotion: FC<GridMotionProps> = ({
  items = [],
  gradientColor = "#dc2626",
}) => {
  const gridRef = useRef<HTMLDivElement>(null);
  const rowRefs = useRef<(HTMLDivElement | null)[]>([]);
  const mouseXRef = useRef<number>(0);

  const totalItems = 28;

  const defaultItems: GridItem[] = Array.from(
    { length: totalItems },
    (_, i) => ({
      image: `https://source.unsplash.com/random/300x300?sig=${i}`,
      label: `Item ${i + 1}`,
    })
  );

  const combinedItems: GridItem[] =
    items.length >= totalItems
      ? items.slice(0, totalItems)
      : [...items, ...defaultItems.slice(items.length, totalItems)];

  useEffect(() => {
    mouseXRef.current = window.innerWidth / 2;
    gsap.ticker.lagSmoothing(0);

    const handleMouseMove = (e: MouseEvent) => {
      mouseXRef.current = e.clientX;
    };

    const updateMotion = () => {
      const maxMoveAmount = 300;
      const baseDuration = 0.8;
      const inertiaFactors = [0.6, 0.4, 0.3, 0.2];

      rowRefs.current.forEach((row, index) => {
        if (row) {
          const direction = index % 2 === 0 ? 1 : -1;
          const moveAmount =
            ((mouseXRef.current / window.innerWidth) * maxMoveAmount -
              maxMoveAmount / 2) *
            direction;

          gsap.to(row, {
            x: moveAmount,
            duration:
              baseDuration + inertiaFactors[index % inertiaFactors.length],
            ease: "power3.out",
            overwrite: "auto",
          });
        }
      });
    };

    const removeAnimationLoop = gsap.ticker.add(updateMotion);
    window.addEventListener("mousemove", handleMouseMove);

    return () => {
      window.removeEventListener("mousemove", handleMouseMove);
      removeAnimationLoop();
    };
  }, []);

  return (
    <div ref={gridRef} className="h-full w-full overflow-hidden">
      <section 
        className="w-full h-screen overflow-hidden relative flex items-center justify-center"
        style={{
          background: `radial-gradient(circle, ${gradientColor} 0%, transparent 100%)`,
        }}
      >
        <div className="absolute inset-0 bg-black/50 z-[3] pointer-events-none"></div>
        <div className="absolute inset-0 pointer-events-none z-[4] bg-[length:250px]"></div>
        <div className="gap-4 flex-none relative w-[150vw] h-[150vh] grid grid-rows-4 grid-cols-1 rotate-[-15deg] origin-center z-[2]">
          {Array.from({ length: 4 }, (_, rowIndex) => (
            <div
              key={rowIndex}
              className="grid gap-4 grid-cols-7"
              style={{ willChange: "transform, filter" }}
              ref={(el) => {
                rowRefs.current[rowIndex] = el;
              }}
            >
              {Array.from({ length: 7 }, (_, itemIndex) => {
                const index = rowIndex * 7 + itemIndex;
                const content = combinedItems[index];
                if (!content) return null;

                return (
                  <div key={itemIndex} className="relative">
                    <div className="relative w-full h-full overflow-hidden rounded-[10px] bg-red-600/80 flex items-center justify-center text-white text-[1.2rem]">
                      {/* Image only or image + text */}
                      {content.image && (
                        <div
                          className="w-full h-full bg-cover bg-center absolute top-0 left-0"
                          style={{ backgroundImage: `url(${content.image})` }}
                        ></div>
                      )}

                      {/* Label - either centered (text-only) or bottom overlay (image + text) */}
                      {content.label && !content.image && (
                        <div className="p-4 z-[1] text-center text-white font-semibold text-xl tracking-wide drop-shadow-md">
                          {content.label}
                        </div>
                      )}
                    </div>
                  </div>
                );
              })}
            </div>
          ))}
        </div>
        <HeroOverlay />
      </section>
    </div>
  );
};

export default GridMotion;
